<template>

    <div class="col-md-12 pb-2 px-0" id="dsfileList">
        <div class="theme-primary partition p-actions-expand tablehead p-actions-collapse">
            <div class="col-sm-4 col-md-6 col-lg-2 col-xl-2 pt-2 float-left text-white">
                <strong></strong>

            </div>
            <div class="col-sm-4 col-md-6 col-lg-2 col-xl-2 pt-2 float-left text-white">
                <strong></strong>

            </div>
            <div class="col-sm-4 col-md-6 col-lg-2 col-xl-2 py-2 float-left text-white">
                <strong></strong>

            </div>
            <div class="col-sm-12 col-md-12 col-lg-6 col-xl-6 float-left text-right p-0 theme-primary">
                <span class="p-actions float-right">
                    <a id="aAddFolder" href="javascript:;" @click="openpopup" class="p-action-btn text-white" title=""
                        data-toggle-tooltip="tooltip" data-original-title="Add">
                        <em class="fa fa-plus text-white"></em> <br>Add Folder
                    </a>
                    <a href="javascript:;" id="aDeleteAll" @click="CommonDelete" title="@Resources.Delete"
                        class="p-action-btn text-white disabled">
                        <em class="fa fa-trash"></em> <br />Delete
                    </a>
                </span>
                <span class="collapse-head-btns">
                    <a href="javascript:;" class="collapse-btns"><em class="fa fa-angle-up text-white"></em> </a>
                </span>

            </div>

        </div>&nbsp;
        <div class="col-md-12 p-0 float-left">

<div class="heading-border">
    <h5 class="h5">
        <span class="font-weight-medium mr-2">{{$t('YouAreInside')}}</span>

        <span class="text-primary border-0 mr-2 font-16">
            <a @click="SpecificFolders('', commonParentName)" class="text-primary">{{
                commonParentName
            }}</a></span>
        <span class=" border-0 font-16" v-for="(item, index) in parentname" :key="index" :class="{'disabled':parentname.length-1==index}"><a
                v-if="parentname[0] != ''" :class="{'disabled text-disabled':parentname.length-1==index, 'text-primary':parentname.length-1!=index}" @click="SpecificFolders(item[1], commonParentName)"
                :folderId="item[1]" > > {{ item[0]}}</a></span>
                <small class="float-right">
            <a href="javascript:void(0)"  v-if="commonParentName!='Recently Added' && commonParentName!='Pinned Folders' && commonParentName!='Shared Folders'"  :class="{ 'disabled': folderPermission == 'r' || commonParentName == 'Shared' }" title="" id="aAddUser"   @click="ShowUpload(isShowUploadDiv)" data-toggle-tooltip="tooltip" data-original-title="Add User">
                <i class="fa fa-upload pr-1"></i>Upload Files/Folder
            </a>                    </small>
    </h5>
</div>
</div>
<fileupload :folderId="folderId" v-if="isShowUploadDiv==true" :isGlobal='1' :checkThis="checkThis" @update-list-after-upload="updatelistafterupload"></fileupload>
        <div class="row">
            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 mb-2 searchpanel tablehead search float-left">
                <div class="w-100 ">
                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 Search-panel float-left  pt-2 p-0">

                                <div class="col-sm-12 col-md-12 col-lg-6 col-xl-6 float-left"> 
                                    <div class="row mt-0">
                                        <div class="col-7 float-left pr-1 pl-0">
                                            <div class="form-group">
                                                <input id="search" maxlength="50" @keydown.enter="SearchGlobalData(commonParentName,searchCondition)"   v-model="searchCondition" class="form-control search" type="text"   placeholder="Search by Folder name, File name, Tag ..." autocomplete="off">
                                            </div>
                                        </div>
                                        <div class="col-5 float-left pl-0">
                                            <div class="background-0 text-left form-check-inline">
                                                <input type="hidden" id="hdnSearch" value="">
                                                <a title="" id="Go"  @click="SearchGlobalData(commonParentName, searchCondition)"  href="javascript:;" class="btn btn-success mr-1 px-4 hlsearch" data-toggle-tooltip="tooltip" data-original-title="Search" aria-describedby="tooltip697211"><i class="fa fa-search"></i></a>
                                                <a href="javascript:;" title="" @click="RefreshPage" class="btn btn-danger clearsearchtext px-4" data-toggle-tooltip="tooltip" data-original-title="Clear all "><i class="fa fa-refresh"></i></a>
                                            </div>
                                        </div>

                                    </div>
                                </div>

                    </div>
                </div>
            </div>               
            <div class="clearfix"></div>
        </div>
        <div class="listing">
            <div class="table-responsive table-fix-header" id="div_forListViews">
                <table class="table table-bordered mt-0" style="width: 100%;" id="tblFileList">
                    <thead class="thead-bg">
                        <tr>
                            <th style="width:50px !important;" v-on:change="onChange()" class="text-center"><input
                                    id="chkAll" class="chkitems" value="" type="checkbox"></th>
                            <th @click="SortData($event, 'Name', 'Sort', commonParentName)">
                                <span class="sort"
                                    :class="{ 'tb_headerSortDown': sortExp == 'ASC' && specificSortClick == 'Name', 'tb_headerSortUp': sortExp == 'DESC' && specificSortClick == 'Name' }">Name</span>
                            </th>
                            <th class="text-center" @click="SortData($event, 'Tags', 'Sort', commonParentName)">
                                <span class="sort"
                                    :class="{ 'tb_headerSortDown': sortExp == 'ASC' && specificSortClick == 'Tags', 'tb_headerSortUp': sortExp == 'DESC' && specificSortClick == 'Tags' }">Tags</span>
                            </th>
                            <th class="text-center" @click="SortData($event, 'CreatedBy', 'Sort', commonParentName)">
                                <span class="sort" :class="{ 'tb_headerSortDown': sortExp == 'ASC' && specificSortClick == 'CreatedBy', 'tb_headerSortUp': sortExp == 'DESC' && specificSortClick == 'CreatedBy' }">Created By</span>
                            </th>
                            <th class="text-center" @click="SortData($event, 'DocSize', 'Sort', commonParentName)">
                                <span class="sort"
                                    :class="{ 'tb_headerSortDown': sortExp == 'ASC' && specificSortClick == 'DocSize', 'tb_headerSortUp': sortExp == 'DESC' && specificSortClick == 'DocSize' }">Size</span>
                            </th>
                            <th class="text-center">
                                <span>Created On</span>
                            </th>
                            <th data-width="5%" style="width:100px;" class="text-center nonresize">
                                <span>Action</span>
                            </th>
                        </tr>
                    </thead>
                    <tbody>

                        <tr v-for="(item,index1) in FilesListingData" :key="index1">
                            <td class="text-center"><input :value="item.Id" :disabled="item.IsDeletePermission != 1"
                                    v-on:change="onChange()" :docCode="item.IsDocCode" v-bind:fileid="item.FileId"
                                    v-bind:filepath="item.FilePath" class="chkItems" type="checkbox"></td>
                                    <td :class="(item.IsDocCode == 1 ? 'file-image doc-icon' : '')">
                                <div class="pin pr-2 d-inline-block">
                                    <em style="cursor:pointer;" :pinId="item.PinId" class="fa fa-thumb-tack"
                                        @click="MarkAsUnPin(item.Id)" v-if="item.PinId > 0" aria-hidden="true"></em>
                                </div>
                                <em class="fa fa-folder pr-2 font-18 text-primary" style="font-size:25px" v-if="item.IsDocCode == 0"
                                    aria-hidden="true"></em>
                                <span class="text-overflow-dynamic-container" v-if="item.IsDocCode == 0">
                                    {{ item.pin_id }}
                                  <a class="aFileDownload" :value="item.Id"
                                  @click="GlobalStorageDocumentListing(item.Id)"
                                        v-bind:folderId="item.Id" v-bind:filepath="item.FilePath"
                                        v-bind:filetype="item.FileType" v-bind:filename="item.FileName"
                                        href="javascript:;" title="">{{ item.NAME }}</a>{{ item.DocExtention }}
                                    <!-- <em class="fa fa-thumb-tack" @click="MarkAsUnPin(item.Id)" v-if="item.PinId > 0" aria-hidden="true"></em>  -->
                                </span>
                               <img style="height: 30px;" v-if="item.DocType != '' && (item.DocExtention=='pdf' || item.DocExtention=='jpg' || item.DocExtention=='png' || item.DocExtention=='jpeg')" :class="GetFileIconClass(item.DocExtention)"
                                :alt='item.FileType' :title='item.DocType' :data-fileType='item.DocExtention' :src="GetThumbnail(item.FileThumbnailPath)" 
                                @click="FilesDetail(item.Id, item.IsDocCode, item.DocPathLocation, item.DocExtention, item.ParentId, item.PERMISSION)"/>
                         
                                <em v-if="item.IsDocCode == 1 && (item.DocExtention=='txt' || item.DocExtention=='docx'|| item.DocExtention=='xlsx' || item.DocExtention=='xls' || item.DocExtention=='mp3' || item.DocExtention=='mp4')" :class="GetFileIconClass(item.DocExtention)" 
                                    width="27px"></em> 
                                <em v-if="item.IsDocCode == 1 && (item.DocExtention!='pdf' && item.DocExtention!='jpg' && item.DocExtention!='png' && item.DocExtention!='jpeg' && item.DocExtention!='txt' && item.DocExtention!='docx'&& item.DocExtention!='xlsx' && item.DocExtention!='xls' && item.DocExtention!='mp3' && item.DocExtention!='mp4')" :class="GetFileIconClass(item.DocExtention)" 
                                width="27px"></em> &nbsp;
                                <span class="text-overflow-dynamic-container" v-if="item.IsDocCode == 1">
                                    <!-- <a class="aFileDownload" :value="item.Id" v-bind:doccode="item.IsDocCode"
                                        v-bind:folderId="item.Id"
                                        @click="FilesDetail(item.Id, item.IsDocCode, item.DocPathLocation, item.DocExtention, item.ParentId)"
                                        v-bind:filepath="item.FilePath" v-bind:filetype="item.FileType"
                                        v-bind:filename="item.FileName" :href="'/ViewFileDetails/' + item.Id+'/'+item.IsDocCode+'/'+item.ParentId+'/'+item.DocExtention+'/'+commonParentName+'/'+refCode  "   title="">{{ item.NAME }}
                                    </a> -->
                                    <RouterLink v-if="item.IsDocCode == 1" class="aFileDownload"
                                            v-bind:id="item.Id" :href="'/ViewFileDetails/' + item.Id+'/'+item.IsDocCode+'/'+item.ParentId+'/'+item.DocExtention+'/'+null+'/'+commonParentName+'/'+refCode "
                                            :to="'/ViewFileDetails/' + item.Id+'/'+item.IsDocCode+'/'+item.ParentId+'/'+item.DocExtention+'/'+null+'/'+commonParentName+'/'+refCode">
                                            {{ item.NAME }}
                                            
                                        </RouterLink>
                                    <!-- <em class="fa fa-thumb-tack" @click="MarkAsUnPin(item.Id)" v-if="item.PinId > 0" aria-hidden="true"></em>  -->
                                </span>
                            </td>
                            <td class="text-center ll">
                                <span class="text-center text-overflow-dynamic-container ll">
                                    {{ item.Tags }}
                                </span>
                            </td>
                            <td class="file-image doc-icon text-center">
                                <span class="text-center">
                                    {{ item.CreatedBy }}
                                </span>
                            </td>
                            <td class="text-center">
                                <span class="text-center text-overflow-dynamic-container">
                                    {{ item.DocSize }}
                                </span>
                            </td>
                            <td class="text-center">
                                <span class="text-center text-overflow-dynamic-container">
                                    {{ convertdateformat(item.created_at) }}
                                </span>
                            </td>
                            <td class="text-center mobile-action" v-if="item.IsDeletePermission == 1">
                                <span class="actions mobileaction rt_25">
                                    <em class="fa fa-ellipsis-h action_icon" aria-hidden="true"></em> 
                                    <span class="action-list-box">
                                        <span class="list-actions w-100" id="action-list">
                                            <a v-if="item.IsDocCode==0" @click="GetFolderDetail(item.Id, item.IsDocCode, item.ParentId)"
                                                href="javascript:;" class="aFileRename actions-onclick">
                                                <em class="fa fa-pencil text-success action-icon"></em> 
                                                <span class="text">Rename</span>
                                            </a>
                                            <a v-if="item.IsDocCode==1" @click="GetFileDetail(item.Id, item.IsDocCode, item.NAME, 0)"
                                                href="javascript:;" class="aFileRename actions-onclick">
                                                <em class="fa fa-pencil text-success action-icon"></em> 
                                                <span class="text">Rename</span>
                                            </a>
                                            <a href="javascript:;" @click="CommonDelete(item.Id, item.IsDocCode)"
                                                class="aFileDelete actions-onclick">
                                                <em class="fa fa-trash text-danger action-icon"></em> 
                                                <span class="text">Delete</span>
                                            </a>
                                            <em class="fa fa-times close close-action" aria-hidden="true"></em> 
                                        </span>
                                    </span>
                                </span>
                            </td>
                        </tr>
                        <tr>
                            <td v-if="FilesListingData.length == 0" colspan="7"
                                class="text-danger text-center no-record">No
                                Record found</td>
                        </tr>
                    </tbody>
                </table>
            
            </div>
            <div class="border-top-pagi">
                <Pager v-bind:numberofrecords="totalRecords" v-on:change="pageSizecommon" prev-text="Prev"
                    first-text="First" last-text="Last" next-text="Next" v-on:SetCurrentPage="pagerMethod"></Pager>
            </div>
        </div>
    </div>
</template>

<script>
import fileupload from "./../components/Common/file-upload.vue"
import DataService from "./../services/DataService"
import Pager from "./Common/Pager.vue"
export default {
    components: {
        Pager,
        fileupload
    },
    data() {
        return {
            pageNo: 1,
            pageSize: 10,
            sortExp: null,
            specificSortClick: null,
            checkThis: this,
            pageSize: 10,
            isShowUploadDiv: false,
            searchCondition: null,
            // folderPermission:''
        }

    },
    props: {
        FilesListingData: {
            type: Array,
            required: true
        },
        parentname: {
            type: Array,
            required: true
        },
        commonParentName: {
            type: String,
            required: true
        },
        folderId: null,
        totalRecords: {
            type: Number,
            required: true
        },
        refCode:{
            type: String,
            required: true
        },
        routeClass:{
            type: String,
            required: false
        },
        TotalPages: {
            type: Number,
            required: true
        },
        folderPermission:{
            type: String,
            required: true
        }
    },
    methods: {
        // To pass variable 
        pagerMethod: function (s) {
           debugger
            // this.PageNumber = value.PageNumber;
            if (parseInt(s.PageNumber) > 0) {
                this.$parent.PagerData(parseInt(s.PageNumber), 'Organization Space',this.specificSortClick, this.sortExp, s.PageSize, this.folderId, this.searchCondition);
            }
            else {
                this.PageNo = 1
            }
        },
        GetThumbnail: function(path){
            var cls=null;
            if(path!=null){
                var incStr = path.includes("wwwroot"); 
            }
            if(incStr==true){
                 cls=path.replace("/wwwroot", "");
            }
            else{
                cls=path;
            }
            return cls;
        },
        SearchGlobalData: function (name, searchCond) {
            
            var vm = this;
            vm.$parent.StorageDocumentListing('', '', '', name, searchCond.trim());
        },
        RefreshPage: function(){
            var vm =this;
            this.$router.push({ path: "/File/DashBoard"});
            window.location.reload("/File/DashBoard");
        },
        pageSizecommon: function (e) {
            ;
            var vm=this;
            var scondition = ""
            if (vm.searchCondition == null) {
                scondition = vm.searchCondition
            }
            else {
                scondition = vm.searchCondition.trim();
            }
            this.pageSize = e.target.value;
            this.$emit("PagerData", vm.PageNo, vm.commonParentName, '', '', vm.pageSize, vm.folderId, scondition);
        },
        CloseUploadPopup: function () {
            var vm = this;
            vm.isShowUploadDiv = false;
        },
        SortData: function (ev, sortBy, className, name) {
            
            var vm = this;
            var clases = ev.target.getAttribute('class');
            vm.specificSortClick = sortBy;
            if (clases == 'sort' || clases == 'sort tb_headerSortUp') {
                vm.sortExp = 'ASC'
            }
            if (clases == 'sort tb_headerSortDown') {
                vm.sortExp = 'DESC'
            }
            // StorageDocumentListing is a function name and rest are the parameter(folderiddata, Parentid, actionVal, name,searchcondition)
            vm.$parent.StorageDocumentListing(vm.folderId, '', '', name, '', sortBy, vm.sortExp, 'Organization');
        },
        SpecificFolders: function (id, name) {
           
            var vm = this;
            vm.$parent.StorageDocumentListing(id, '', '', name, '', '', vm.sortExp, 'Organization');
             this.$router.push({ path: "/File/DashBoard", query: { FolderId: id,ParentName:'Organization Space',RefCode:'Organization' } });
        },
        convertdateformat: function (date) {
            const event = new Date(date);
            return event.toISOString().slice(0, 10).replace("-", "/").replace("-", "/");
        },
        //Emit Method Dashboard.vue(Pin Folder and files)
        MarkAsPin: function () {
            //
            var vm = this;
            vm.$parent.MarkAsFavourite()
        },
        //Emit Method Dashboard.vue(UnPin Folder and files)
        MarkAsUnPin: function (val) {
            var vm = this;
            vm.$parent.MarkAsUnpins(val)
        },
        //Emit Method Dashboard.vue(To open manage folder popup)
        openpopup: function () {
            this.$parent.ManageOpen("Add",1);
        },
        OpenAddTag: function () {
            //
            this.$parent.ManageTagopen();
        },
        GetSharedFiles() {
            //
            var vm = this;
            this.listingdata.forEach(function (item, index) {
                var sharedWithUsers = '';
                var SharedFilesWith = item.SharedWith ? JSON.parse(item.SharedWith) : null;
                if (SharedFilesWith) {

                    sharedWithUsers = `<table class='table table-striped table-sm mb-0'><thead><tr><th>User Name</th><th>Permission</th></tr></thead>
                                <tbody class='sharedusertable'>`
                    for (var i = 0; i < SharedFilesWith.length; i++) {
                        sharedWithUsers += `<tr><td data-UserId='${SharedFilesWith[i]["UserName"]}'>${SharedFilesWith[i]["UserName"]}</td><td>${SharedFilesWith[i]["permission"]}</td></tr>`
                    }
                    sharedWithUsers += `</tbody> </table>`;
                }
                item.SharedFilesWith = SharedFilesWith;
                item.sharedWithUsers = sharedWithUsers;
            });
        },
        //Emit method Dashboard.vue(Edit Folder)
        GetFolderDetail(fid, doccode, parentId) {
            
            var vm = this;
            //vm.$emit("GetFolderDetail",fid,doccode);
            vm.$parent.GetFolderDetail(fid, doccode, parentId, 1)
            vm.$parent.ManageOpen();
        },
        //Emit method Dashboard.vue(Specific FolderListing)
        GlobalStorageDocumentListing: function (folderId) {
            
            var vm = this;
            vm.$parent.StorageDocumentListing(folderId, '', 1, 'Organization Space', '', '', '', 'Organization');
            this.$router.push({ path: "/File/DashBoard", query: { FolderId: folderId,ParentName:'Organization Space',RefCode:'Organization' } });
        },
        SharedStorageDocumentListing: function (folderId) {
            
            var vm = this;
            vm.$emit("SharedStorageDocumentListing", folderId, '', '', 'Shared Folder');
        },
        SharedStorageDocumentListing: function (folderId) {
            
            var vm = this;
            vm.$emit("SharedStorageDocumentListing", folderId);
        },
        //View File Detail
        FilesDetail: function (fileId, IsDocCode, url, doctype, fId) {
            var vm = this;
            vm.$router.push({ name: 'ViewFileDetails', path: '/ViewFileDetails', params: { FileId: fileId, docCode: IsDocCode, Path: url, fileType: doctype==undefined?null:doctype, FolderId: fId ,CommonParentName:this.commonParentName ,RefCode:this.refCode, RouteClass:this.routeClass==undefined?null:this.routeClass} });
        },
        onChange: function () {
            var checkCount = $('.chkItems:checked').not("[id^='chkAll']").length;
            if (checkCount > 0) {
                $('#assignTag').removeClass('disabled');
                $('#editFolder').removeClass('disabled');
                $('#moveFolder').removeClass('disabled');
                $('#aDeleteAll').removeClass('disabled');
                $('#aShareAll').removeClass('disabled');
                $('#pinUnpin').removeClass('disabled');

            }
            else {
                $('#assignTag').addClass('disabled');
                $('#editFolder').addClass('disabled');
                $('#moveFolder').addClass('disabled');
                $('#aDeleteAll').addClass('disabled');
                $('#aShareAll').addClass('disabled');
                $('#pinUnpin').addClass('disabled');
            }
        },
        //Delete Files and Folder
        CommonDelete: function (Id, doccode) {
            
            var vm = this;
            var values = "";   //using to delete folder
            var files = "";      //using to delete files
            if (doccode == 0) {
                values = Id;
            }
            if (doccode == 1) {
                files = Id;
            }
            $('#tblFileList .chkItems:checked').not("[id^='chkAll']").each(function () {
                if ($(this).attr("doccode") == 0) {
                    if (values.length > 0) {
                        values += ',';
                    }

                    values += $(this).val();
                }
                if ($(this).attr("doccode") == 1) {
                    if (files.length > 0) {
                        files += ',';
                    }

                    files += $(this).val();
                }
            });
            var str = "";
            vm.confirmR(vm.$t("ConfirmDeleteRecord"), true, (vm.$t("Delete") + "  " + vm.$t("Groups")), function () {
                if (values.length > 0 || values > 0) {
                    // vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Groups")), function () {
                    str += values;
                    let params = {};
                    params["ids"] = str;
                    params["refCode"] = "Doc_Folder";
                    params["companyId"] = vm.loggedcompanyid;
                    params["userId"] = vm.loggeduserid;
                    params["moduleName"] = "DocStorage";
                    DataService.CommonDelete(params).then(function (response) {

                        var box = "";
                        if (response.data[0].Status == "Success") {
                            $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            $("[id^='ChangeOwnerId']").removeClass('enable').addClass('disabled');
                            $("[id^='AdditionalOwnerId']").removeClass('enable').addClass('disabled');
                            $("[id^='CloseLeadId']").removeClass('enable').addClass('disabled');
                            $("[id^='DeleteMultiple']").removeClass('enable').addClass('disabled');
                            box = vm.ShowAlert([vm.$t('Folder')], "success", true, vm.$t("Alert"));
                            
                           
                              
               
                            vm.$emit("StorageDocumentListing",vm.folderId);
                            vm.$emit("GetOrganizationSpace");
                        }
                        else {
                            box = box = vm.ShowAlert(vm.$t("DeletedError", [vm.$t('Folder')]), "success", true, vm.$t("Alert"));
                        }
                        // setTimeout(function () {
                        //     box.modal('hide');
                        // }, 2000);

                    },);
                    // });
                }
                if (files.length > 0 || files > 0) {
                    str += files + ',';
                    let params = {};
                    params["ids"] = str;
                    params["refCode"] = "Doc_File";
                    params["companyId"] = vm.loggedcompanyid;
                    params["userId"] = vm.loggeduserid;
                    params["moduleName"] = "DocStorage";
                    DataService.CommonDelete(params).then(function (response) {

                        var box = "";
                        if (response.data[0].Status == "Success") {
                            $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            $("[id^='ChangeOwnerId']").removeClass('enable').addClass('disabled');
                            $("[id^='AdditionalOwnerId']").removeClass('enable').addClass('disabled');
                            $("[id^='CloseLeadId']").removeClass('enable').addClass('disabled');
                            $("[id^='DeleteMultiple']").removeClass('enable').addClass('disabled');
                            box = vm.ShowAlert('File'+ vm.$t('DeletedSuccess'),"success");
                            vm.$emit("StorageDocumentListing");
                        }
                        else {
                            box = alertR(vm.$t('DeletedError'), vm.$t('Groups'));
                        }
                        // setTimeout(function () {
                        //     box.modal('hide');
                        // }, 2000);

                    },);
                }
                // else {
                //     if(values>0){

                //         alert(vm.$t("Selectanyrecordtodelete"));
                //     }
                // }
            })

        },
        //open popup for share document
        popup: function (Id, doccode) {

            var vm = this;
            var values = "";
            var files = "";
            if (doccode == 0) {
                values = Id;
            }
            if (doccode == 0) {
                files = Id;
            }
            $('#tblFileList .chkItems:checked').not("[id^='chkAll']").each(function () {
                if ($(this).attr("doccode") == 0) {
                    if (values.length > 0) {
                        values += ',';
                    }

                    values += $(this).val();
                }
                if ($(this).attr("doccode") == 1) {
                    if (files.length > 0) {
                        files += ',';
                    }

                    files += $(this).val();
                }
            });

            var str = "";
            if (values.length > 0 || values > 0 || files.length > 0 || files > 0) {
                this.$parent.ShareDoc()
            }
            else {
                alert(this.$t("Selectanyrecordtodelete"));
            }

        },
        OpenAddGroupPopup: function () {
            this.$emit("ModalAddGroup")
        },
        OpenGroupListing: function () {
            this.$emit("ModalGroupListing")
        },


        openmovefolderpopup: function () {
            this.$parent.ModalmoveFolder();
        },
        MoveFoldersFiles: function (id, docCode) {
            var vm = this;
            vm.$parent.ModalmoveFolder(id, docCode);
        },
        GetFileDetail: function (id, doccode, folderName, renamid) {
            
            var vm = this;
            vm.$parent.GetFileDetails(id, doccode, folderName, null, renamid)
            vm.$parent.ManageOpen(renamid)
            // vm.$emit("ManageOpen", renamid)
        },
        ShowUpload: function(data){
            
            var vm=this;
            if(data==false){
                vm.isShowUploadDiv=true;
            }
            else{
                vm.isShowUploadDiv=false;
            }
        },
        GetFileIconClass: function (ext) {
            ext=ext.toLowerCase();
            var fileclass = "";
            if (ext == 'png' || ext == "jpeg" || ext == "jpg" || ext == "JPG" || ext == "jfif") {
                fileclass = "fa fa-file-image-o";
            }
            else if (ext == "pdf") {
                fileclass = "fa fa-file-pdf-o"
            }
            else if (ext == "xlsx") {
                fileclass = "fa fa-file-excel-o"
            }
            else if (ext == "txt") {
                fileclass = "fa fa-file-text-o"
            }
            else if (ext == "xls") {
                fileclass = "fa fa-file-excel-o"
            }
            else if (ext == "docx") {
                fileclass = "fa fa-file-word-o"
            }
            else if (ext == "mp3") {
                fileclass = "fa fa-file-audio-o"
            }
            else if(ext=="mp4"){
                fileclass = "fa fa-file-video-o"
            }
            else{
                fileclass="fa fa-file-o"
            }
            return fileclass
        },
        updatelistafterupload(folderId) {
            this.$emit("StorageDocumentListing", folderId);
        }
    }
}
</script>
